.components {

	margin-bottom: 10vh;
	overflow: hidden;
}

@media ($min-md) {
	.components {
		margin-bottom: 14vh;


		& .case {

			&:before {
				content: '';
				position: absolute;
				width: 100vw;
				height: calc(100% - 20vh);
				margin: 10vh -0px;
				top: 0;
				bottom: 0;
				left: 50%;
				background-image: url(../img/admin-bg.jpg);
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center;
				z-index: -1;
				transform: translateX(-50%);
			}

		}
	}
}
